import React, {Component} from 'react'

const TableHeader = ()=> {
	return(
		<thead>
		<tr>
			<th>name</th>
			<th>job</th>
			<th>remove</th>
		</tr>
		</thead>
	);
}

const TableBody = (props)=> {

	const {removeCharacter} = props
	const rows = props.charactersData.map((row,index)=>{
	return(
			<tr key = {index}>
				<td>{row.name}</td>
				<td>{row.job}</td>
				<td><button onClick={() => removeCharacter(index)}>delete!</button></td>
			</tr>
		);
	})
	return <tbody>{rows}</tbody>
}

class Table extends Component {
	render(){
		
		const {charactersData} = this.props
		const {removeCharacter} = this.props
		
		return(
			<table>
			<TableHeader />
			<TableBody charactersData={charactersData} removeCharacter={removeCharacter} />
			</table>
		);
	}
}

export default Table